



























































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

import { Emap as Model, EmapItem } from '../models';

@Component({
    components: {},
})
export default class EmapDeviceListSource extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => new EmapItem(),
    })
    value: EmapItem;

    @Prop({
        type: Object,
        required: false,
        default: () => {
            return {
                image: false,
                map: false,
                list: false,
            };
        },
    })
    dropTarget: { image: boolean; map: boolean; list: boolean };

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;
    //#endregion prop

    //#region variable
    private eEmapDeviceType = Model.EEmapDeviceType;

    private toggleItem: {
        isACDropdownActive: boolean;
        isDeviceType2DropdownActive: boolean;
        isDeviceType3DropdownActive: boolean;
    } = {
        isACDropdownActive: true,
        isDeviceType2DropdownActive: true,
        isDeviceType3DropdownActive: true,
    };
    //#endregion variable

    //#region
    @Watch('value', { immediate: true, deep: true })
    private valueChanged(newVal: EmapItem, oldVal: EmapItem): void {}
    //#endregion watch

    //#region Drag & Drop
    private dropList(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }

        this.dropTarget.list = false;
        if (!this.value.focusDevice || !this.value.focusDevice.objectId) {
            return false;
        }
        if (this.value.focusDevice.deviceType === Model.EEmapDeviceType.ac) {
        }
        this.value.focusDevice.positionShow = null;
        this.value.focusDevice.positionScale = null;
    }

    private dragListOver(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.list = true;
    }

    private dragListLeave(event: any): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.list = false;
    }

    private dragDeviceStart(event: any, device: Model.IEmapDevice): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.value.dropItem.device = device;
        this.value.focusDevice = device;
        if (event.offsetX && event.offsetY) {
            this.value.dropItem.mousePosition.x = event.offsetX;
            this.value.dropItem.mousePosition.y = event.offsetY;
        }
        if (event.srcElement && event.srcElement.clientWidth && event.srcElement.clientHeight) {
            this.value.dropItem.size.width = event.srcElement.clientWidth;
            this.value.dropItem.size.height = event.srcElement.clientHeight;
        }
    }

    private dragDeviceOver(event: any, source: Model.IEmapDevice): void {
        // 判斷上很常觸發, 所以先不做事
        // this.value.clearDropItem();
    }
    //#endregion Drag & Drop

    //#region source
    private toggleDeviceList(type: Model.EEmapDeviceType): void {
        let tempDeviceEmapData = JSON.parse(JSON.stringify(this.value.acEmapData));
        switch (type) {
            case Model.EEmapDeviceType.ac:
                this.toggleItem.isACDropdownActive = !this.toggleItem.isACDropdownActive;
                tempDeviceEmapData = JSON.parse(JSON.stringify(this.value.acEmapData));
                this.value.acEmapData = tempDeviceEmapData;
                break;
            case Model.EEmapDeviceType.device_type2:
                this.toggleItem.isDeviceType2DropdownActive = !this.toggleItem.isDeviceType2DropdownActive;
                tempDeviceEmapData = JSON.parse(JSON.stringify(this.value.device_type2EmapData));
                this.value.device_type2EmapData = tempDeviceEmapData;
                break;
            case Model.EEmapDeviceType.device_type3:
                this.toggleItem.isDeviceType3DropdownActive = !this.toggleItem.isDeviceType3DropdownActive;
                tempDeviceEmapData = JSON.parse(JSON.stringify(this.value.device_type3EmapData));
                this.value.device_type3EmapData = tempDeviceEmapData;
                break;
            default:
                break;
        }
    }

    private showDeviceActive(device: Model.IEmapDevice): boolean {
        return device.positionShow !== null && !isNaN(device.positionShow.x) && !isNaN(device.positionShow.y);
    }

    private showDeviceSelection(device: Model.IEmapDevice): boolean {
        if (this.value.focusDevices.length > 0) {
            return !!this.value.focusDevices.find((n) => n.objectId === device.objectId);
        }

        return device.objectId === this.value.focusDevice.objectId;
    }
    //#endregion source

    //#region resolve
    private resolveIsDraggable(): boolean {
        return !this.modifyDisabled;
    }

    private resolveDeviceKey(key: string, device: Model.IEmapDevice): string {
        return `${key}-${device.index}`;
    }
    //#endregion resolve

    //#region emit
    private clickDevice(event: any, device: Model.IEmapDevice): void {
        this.$emit('click-device', event, device);
    }

    private inputModel(): void {
        this.$emit('input', this.value);
    }
    //#endregion emit
}
