





























































































import { Vue, Component } from 'vue-property-decorator';

import { ServiceResponseFilter, ServiceDialog, ServiceToast } from '@/../helpers';
import { IComponent } from '@/../models';

import { ModelsEmap as Model } from './models';

import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';
import Loading from '@/../components/loading/loading.vue';

// E-Map
import Emap from '@/components/e-map/e-map.vue';
import { Emap as ModelEmap, EmapItem } from '@/components/e-map/models';
import { EmapDialog } from '@/components/e-map/helps/dialog';

@Component({
    components: { Card, Forms, Emap },
})
export default class VuePageClass extends Vue {
    //#region Variables

    private objectId: string = '60dd33a7e562d74ab8e0a3f3';
    private mapData: Model.IMapResponse = Model.EmapFakeData;
    private loading = new Loading();

    //#region E-Map
    private isEditEmap: boolean = false;
    private eEmapType = ModelEmap.EEmapType;
    private eEmapMode = ModelEmap.EEmapMode;
    private emapModifyDisabled = false;
    private eMapItem: EmapItem = new EmapItem();
    //#endregion E-Map
    //#endregion

    //#region Computed form
    private get formSaveDisable(): boolean {
        return !this.eMapItem.backgroundItem.image;
    }

    private get mapDisable(): boolean {
        return !this.eMapItem.backgroundItem.image;
    }
    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private created(): void {
        this.initEmapData(this.objectId);
    }
    private beforeMount(): void {}
    private async mounted(): Promise<void> {
        this.initEmap();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region init
    private initEmapData(objectId: string = ''): void {
        if (objectId) {
            try {
                const status: number = 200;
                const data: Model.IMapR = Model.EmapFakeData;
                if (status === 200) {
                    this.mapData = data;
                    this.initEmap();
                }
            } catch (e) {
                ServiceResponseFilter.catchError(this, e);
            }
        } else {
            this.initEmap();
        }
    }

    //#endregion init

    private clickEditEmap(): void {
        this.isEditEmap = true;
        this.eMapItem.clearfocusDevice();
        this.eMapItem.clearfocusDevices();
    }

    private async clickViewEmap(): Promise<void> {
        EmapDialog.Remove(this.$i18n.Emap_CancelContent, this.$i18n.Emap_CancelTitle, this.$i18n.Emap_RemoveButtonYes, this.$i18n.Emap_RemoveButtonNo)
            .then(async (result: any) => {
                if (result.isDismissed) {
                    this.eMapClear();
                    await this.initEmapData();
                    this.isEditEmap = false;
                }
            })
            .catch((e: any) => {
                ServiceResponseFilter.catchError(this, e);
            });
    }
    //#endregion click

    //#region Emap
    private async initEmap(): Promise<void> {
        this.eMapClear();
        this.eMapItem.i18n = {
            or: this.$i18n.Emap_Or,
            previewButtonAddMap: this.$i18n.Emap_Preview_ButtonAddMap,
            previewButtonViewMap: this.$i18n.Emap_Preview_ButtonViewMap,
            modifyEmptyTitle: this.$i18n.Emap_Modify_EmptyTitle,
            modifyEmptyContent: this.$i18n.Emap_Modify_EmptyContent,
            modifyButtonUploadImage: this.$i18n.Emap_Modify_ButtonUploadImage,
            removeTitle: this.$i18n.Emap_RemoveTitle,
            removeContent: this.$i18n.Emap_RemoveContent,
            removeButtonNo: this.$i18n.Emap_RemoveButtonNo,
            removeButtonYes: this.$i18n.Emap_RemoveButtonYes,
            deviceSourceListTitle: this.$i18n.Emap_DeviceSourceListTitle,
            deviceSourceListSubTitle: this.$i18n.Emap_DeviceSourceListSubTitle,
        };

        let acIndex: number = 0;
        let acEmapData: ModelEmap.IEmapData = ModelEmap.DefaultEmapData;

        // ac device List
        acEmapData.objectId = this.objectId ?? '';
        acEmapData.name = 'emap device' ?? '';
        acEmapData.imageSrc = this.mapData.imageSrc ?? '';
        acEmapData.devices = [];

        if (this.mapData.imageSrc) {
            const base64: string = this.mapData.imageSrc;

            if (base64) {
                this.eMapItem.backgroundItem.image = base64;
                this.resetEmapScale();
            }
        }

        (this.mapData.devices || []).forEach((deviceDetail, index) => {
            acIndex++;
            let tempDevice: ModelEmap.IEmapDevice = {
                objectId: deviceDetail?.device?.objectId ?? '',
                name: deviceDetail?.device?.name ?? '',
                index: acIndex.toString().padStart(2, '0') ?? '',
                deviceType: ModelEmap.EEmapDeviceType.ac,
                positionScale: {
                    horizontal: deviceDetail?.x ?? 0,
                    vertical: deviceDetail?.y ?? 0,
                },
                positionShow: null,
            };
            acEmapData.devices.push(tempDevice);
        });

        this.eMapItem.acEmapData = acEmapData;
    }

    private eMapReset() {
        this.eMapItem.resetSelf();
    }

    private eMapClear() {
        this.eMapItem.clearSelf();
    }

    private resetEmapScale() {
        let emapModifyRef: any = this.$refs.emapModify;
        setTimeout(() => {
            if (emapModifyRef) {
                emapModifyRef.setScale();
            }
        }, 500);
    }

    private async clickDevice(focusDevices: ModelEmap.IEmapDevice[]): Promise<void> {
        console.log(`clickDevice => `, focusDevices);
    }
    //#endregion

    //#region Server API (save, delete, test)
    private async dataDelete(): Promise<void> {
        const content: string = this.$i18n.Emap_ConfirmDelete;
        const title: string = this.$i18n.Dialog_Confirm;

        let param: { data: { objectId: string[] } } = { data: { objectId: [this.objectId] } };

        //   double comfirm
        ServiceDialog.Question(this, content, title)
            .then(async (result) => {
                if (result.value) {
                    this.loading.show();
                    try {
                        this.mapData = JSON.parse(JSON.stringify(Model.EmapFakeDataDefault));
                        const status: number = 200;
                        const data = { datas: [{ statusCode: 200, objectId: '60dd33a7e562d74ab8e0a3f3', message: '' }] };

                        if (status === 200) {
                            this.dataCUDSuccess(data?.datas, this.$i18n.Dialog_DeleteSuccess, 'delete');
                        }
                    } catch (e) {
                        ServiceResponseFilter.catchError(this, e);
                    } finally {
                        this.loading.hide();
                    }
                }
            })
            .catch((e: any) => console.error(e));
    }

    private async dataSave(): Promise<void> {
        const { acEmapData, backgroundItem }: EmapItem = this.eMapItem;

        let dataParam: Model.ISaveData = {
            objectId: this.objectId,
            imageBase64: backgroundItem.image,
            devices: [],
        };

        (acEmapData.devices || []).forEach((device) => {
            if (device.positionShow) {
                dataParam.devices.push({
                    deviceId: device.objectId,
                    x: device.positionScale.horizontal,
                    y: device.positionScale.vertical,
                    isSet: true,
                });
            } else {
                dataParam.devices.push({
                    deviceId: device.objectId,
                    isSet: false,
                });
            }
        });

        let devices: Model.IMapDevice[] = [];

        for (const x of Model.EmapFakeDataDefault.devices) {
            for (const y of dataParam.devices) {
                if (x.device.objectId === y.deviceId) {
                    if (y.x !== undefined && y.x !== null && y.y !== undefined && y.y !== null) {
                        devices.push({
                            device: { objectId: x.device.objectId, name: x.device.name },
                            isSet: true,
                            x: y.x,
                            y: y.y,
                        });
                    } else {
                        devices.push({
                            device: { objectId: x.device.objectId, name: x.device.name },
                            isSet: false,
                        });
                    }
                }
            }
        }

        const param: { datas: Model.ISaveData[] } = { datas: [dataParam] };

        this.loading.show();

        try {
            const status: number = 200;
            const data = { datas: [{ statusCode: 200, objectId: '60dd33a7e562d74ab8e0a3f3', message: '' }] };

            this.mapData.imageSrc = dataParam.imageBase64;
            this.mapData.devices = devices;

            if (status) {
                this.dataCUDSuccess(data?.datas, this.$i18n.Dialog_SaveSuccess);
            }
        } catch (e) {
            ServiceResponseFilter.catchError(this, e);
        } finally {
            this.loading.hide();
        }
    }

    private async dataCUDSuccess(data: IComponent.IStatusCodeObjectIdMessageResponse[], toastMessage: string, action?: string): Promise<void> {
        let total: number = data.length;
        let success: number = 0;
        let fail: number = 0;

        (data || []).forEach(async (item) => {
            if (item.statusCode === 200) {
                success++;
            } else {
                fail++;
                if (item.statusCode === 401 || item.statusCode === 403) {
                    ServiceDialog.ErrorConfirmOnly(this, `${item.message}\r\n`, this.$i18n.Status_Error)
                        .then((result) => {})
                        .catch((e: any) => console.error(e));
                } else {
                    ServiceDialog.ErrorConfirmOnly(this, `${item.message}\r\n`, this.$i18n.Status_Error)
                        .then((result) => {
                            if (result.value) return false;
                        })
                        .catch((e: any) => console.error(e));
                }
            }
        });

        const toastMsg: string = `${toastMessage}. ${this.$i18n.Comman_SuccessCount}: ${success}`;
        if (action === 'delete') {
            if (total === success) {
                this.isEditEmap = false;
                this.eMapClear();
                ServiceToast.success(toastMsg);
            } else {
                this.isEditEmap = false;
                ServiceToast.success(toastMsg);
            }
        } else {
            if (total === success) {
                ServiceToast.success(toastMsg);
            }
        }
    }
    //#endregion
}
