














































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

// Model

import { Emap as Model, EmapItem } from './models';

// Component
import EmapPreviewEmptySource from './vues/preview-empty-source.vue';

import EmapImageBoxEmpty from './vues/image-box-empty.vue';
import EmapImageBoxSource from './vues/image-box-source.vue';

import EmapDeviceListSource from './vues/device-list-source.vue';

@Component({
    components: {
        EmapPreviewEmptySource,

        EmapImageBoxEmpty,
        EmapImageBoxSource,

        EmapDeviceListSource,
    },
})
export default class Emap extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: String,
        required: false,
        default: () => {
            return Model.EEmapType.none;
        },
    })
    type: Model.EEmapType;

    @Prop({
        type: String,
        required: false,
        default: () => {
            return Model.EEmapMode.none;
        },
    })
    mode: Model.EEmapMode;

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    multipleClickDisabled: boolean;
    //#endregion prop

    //#region watch
    @Watch('value.backgroundItem.image', { immediate: true })
    private onBackgroundChange(newval: EmapItem, oldval: EmapItem) {
        this.setScale();
    }
    //#endregion watch

    //#region variable
    private dropTarget: {
        image: boolean;
        map: boolean;
        list: boolean;
    } = {
        image: false,
        map: false,
        list: false,
    };

    private enumItem = {
        eEmapType: Model.EEmapType,
        eEmapMode: Model.EEmapMode,
    };
    //#endregion variable

    //#region Vue Life
    private mounted(): void {
        this.setScale();
        let self = this;
        window.onresize = function (event) {
            self.setScale();
        };
    }
    //#endregion Vue Life

    //#region scale
    private setScale(): void {
        let imageBoxSourceRef: any = this.$refs.imageBoxSource;

        if (imageBoxSourceRef) {
            imageBoxSourceRef.setScale();
        }
    }
    //#endregion scale

    //#region check & change
    private checkTypes(types: Model.EEmapType[]): boolean {
        let result: boolean = false;
        for (let type of types) {
            if (this.type == type) {
                result = true;
                break;
            }
        }
        return result;
    }

    private checkMode(mode: Model.EEmapMode): boolean {
        return mode === this.mode ? true : false;
    }

    private checkImage(): boolean {
        return this.value.backgroundItem.image !== '';
    }
    //#endregion check & change

    //#region click
    private clickDevice(event: any, source: Model.IEmapDevice): void {
        if (this.multipleClickDisabled) {
            this.value.clearfocusDevices();

            if (this.value.focusDevice.objectId === source.objectId) {
                this.value.clearfocusDevice();
            } else if (this.resolveIsDraggable()) {
                this.value.focusDevice = source;
            } else {
                this.value.clearfocusDevice();
            }
        } else {
            this.value.clearfocusDevice();

            if (!!this.value.focusDevices.find((n) => n.objectId === source.objectId)) {
                this.value.clearfocusDevices(source);
            } else if (this.resolveIsDraggable()) {
                this.value.focusDevices.push(source);
            } else {
                this.value.clearfocusDevices(source);
            }
        }
        this.$emit('click-device', this.value.focusDevices);
    }
    //#endregion click

    //#region resolve
    private resolveIsDraggable(): boolean {
        // return this.value.backgroundItem.image ? true : false;
        return true;
    }
    //#endregion resolve

    //#region emit
    private inputModel() {
        this.$emit('input', this.value);
    }

    private addEmap() {
        this.$emit('modify-emap');
    }

    private viewMap() {
        this.$emit('modify-emap');
    }
    //#endregion emit

    //#region reset
    public resetSourceListType() {
        let deviceListSourceRef: any = this.$refs.deviceListSource;
        if (deviceListSourceRef) {
            deviceListSourceRef.clickDeviceType(Model.EEmapDeviceType.ac);
        }
    }

    public resetBindingListServerType() {
        let deviceListBindingRef: any = this.$refs.deviceListBinding;
        if (deviceListBindingRef) {
            deviceListBindingRef.initFocusServer();
        }
    }
    //#endregion reset
}

Vue.component('e-map', Emap);
