import { Emap as Model } from './index';

export class EmapItem {
    //#region i18n
    private _i18n: Model.IEmapI18N = {
        or: 'or',
        previewButtonAddMap: 'Add Map',
        previewButtonViewMap: 'View Map',
        modifyEmptyTitle: 'Upload Image of the map',
        modifyEmptyContent: 'Drag & Drop image here',
        modifyButtonUploadImage: 'Click here to upload map',
        removeTitle: 'Are you sure you want to remove this map?',
        removeContent: 'Removing this map will undo all saved device bindings including video device bindings and event source bindings.',
        removeButtonNo: "No, I don't want lose my data",
        removeButtonYes: "Yes, I know what I'm doing",
        deviceSourceListTitle: 'Source Device',
        deviceSourceListSubTitle: 'Drag and drop source devices to the map',
    };
    //#endregion i18n

    //#region Image
    private backgroundItemOriginal: Model.IEmapBackgroundItem = {
        image: '',
        showSize: { width: 0, height: 0 },
    };
    public backgroundItem: Model.IEmapBackgroundItem = { ...this.backgroundItemOriginal };
    //#endregion Image

    //#region Drag & Drop
    private dropItemOriginal: Model.IEmapDropItem = {
        device: null,
        mousePosition: { x: 0, y: 0 },
        size: { width: 0, height: 0 },
    };
    public dropItem: Model.IEmapDropItem = { ...this.dropItemOriginal };
    //#endregion Drag & Drop

    //#region Server
    public acEmapData: Model.IEmapData = Model.DefaultEmapData;
    public device_type2EmapData: Model.IEmapData[] = [];
    public device_type3EmapData: Model.IEmapData[] = [];
    //#endregion IEmapData

    //#region Focus Devices
    public focusDevices: Model.IEmapDevice[] = [];

    private focusDeviceOriginal: Model.IEmapDevice = {
        objectId: '',
        index: '',
        name: '',
        positionScale: null,
        positionShow: null,
        deviceType: Model.EEmapDeviceType.ac,
    };
    public focusDevice: Model.IEmapDevice = { ...this.focusDeviceOriginal };
    //#endregion Focus Devices

    constructor() {}

    //#region i18n get set
    get i18n() {
        return this._i18n;
    }

    set i18n(value: Model.IEmapI18N) {
        if (value !== null) {
            let self = this;
            Object.keys(value).map(function(objectKey, index) {
                if (self._i18n[objectKey]) {
                    self._i18n[objectKey] = value[objectKey];
                }
            });
        }
    }
    //#endregion i18n get set

    //#region reset
    public resetSelf(): void {
        this.clearDropItem();
        this.clearfocusDevice();
        this.clearfocusDevices();
        this.clearDevice();
    }

    public resetSourcePosition(): boolean {
        if (
            !this.backgroundItem ||
            !this.backgroundItem.showSize ||
            !this.backgroundItem.showSize.width ||
            !this.backgroundItem.showSize.height ||
            this.backgroundItem.showSize.width <= 0 ||
            this.backgroundItem.showSize.height <= 0
        ) {
            return false;
        }

        let backgroundWidth: number = this.backgroundItem.showSize.width;
        let backgroundHeight: number = this.backgroundItem.showSize.height;

        // device type ac
        (this.acEmapData.devices || []).forEach((device, index) => {
            if (device?.positionScale?.horizontal && device?.positionScale?.vertical) {
                if (!device.positionShow) {
                    device.positionShow = { x: 0, y: 0 };
                }
                device.positionShow.x = device.positionScale.horizontal * backgroundWidth;
                device.positionShow.y = device.positionScale.vertical * backgroundHeight;
            }
        });
    }
    //#endregion reset

    //#region clear
    public clearSelf(): void {
        this.clearBackgroundItem();
        this.clearDropItem();
        this.clearfocusDevice();
        this.clearfocusDevices();
        this.clearDevice();
    }

    public clearDropItem(): void {
        this.dropItem = { ...this.dropItemOriginal };
    }

    public clearBackgroundItem(): void {
        this.backgroundItem = { ...this.backgroundItemOriginal };
    }

    public clearfocusDevices(): void
    public clearfocusDevices(device: Model.IEmapDevice): void
    public clearfocusDevices(device?: Model.IEmapDevice): void {
        if (!!device) {
            this.focusDevices = this.focusDevices.filter((n) => n.objectId !== device.objectId);
        } else {
            this.focusDevices = [];
        }
    }

    public clearfocusDevice(): void {
        this.focusDevice = { ...this.focusDeviceOriginal };
    }

    public clearDevice(): void {
        (this.acEmapData.devices || []).forEach((device, index) => {
            device.positionShow = null;
            device.positionScale = null;
        });
    }
    //#endregion clear
}
