export namespace Emap {
    export const DefaultEmapData: IEmapData = {
        objectId: '',
        name: '',
        imageSrc: '',
        devices: [],
    };

    export interface IEmapPosition {
        x: number;
        y: number;
    }

    export interface IEmapSize {
        width: number;
        height: number;
    }

    export interface IEmapScale {
        horizontal: number;
        vertical: number;
    }

    export interface IEmapDropItem {
        device: IEmapDevice | null;
        mousePosition: IEmapPosition;
        size: IEmapSize;
    }

    export interface IEmapData {
        objectId: string;
        name: string;
        imageSrc?: string;
        devices: IEmapDevice[];
    }

    export interface IEmapDevice {
        objectId: string;
        index: string;
        name: string;
        positionScale: IEmapScale;
        positionShow: IEmapPosition | null;
        deviceType: EEmapDeviceType;
    }

    export interface IEmapBackgroundItem {
        image: string;
        showSize: IEmapSize;
    }

    export interface IEmapI18N {
        or: string;
        previewButtonAddMap: string;
        previewButtonViewMap: string;
        modifyEmptyTitle: string;
        modifyEmptyContent: string;
        modifyButtonUploadImage: string;
        removeTitle: string;
        removeContent: string;
        removeButtonNo: string;
        removeButtonYes: string;
        deviceSourceListTitle: string;
        deviceSourceListSubTitle: string;
    }

    export enum EEmapType {
        none = 'none',
        source = 'source',
    }

    export enum EEmapMode {
        none = 'none',
        preview = 'preview',
        modify = 'modify',
    }

    export enum EEmapDeviceType {
        ac = 'ac',
        device_type2 = 'device_type2',
        device_type3 = 'device_type3',
    }
}
