










































































import { Vue, Component, Prop } from 'vue-property-decorator';

import { Emap as Model, EmapItem } from '../models';
import { EmapDialog } from '../helps/dialog';

@Component({
    components: {},
})
export default class EmapImageBoxSource extends Vue {
    //#region prop
    @Prop({
        type: Object,
        required: false,
        default: () => {
            return new EmapItem();
        },
    })
    value: EmapItem;

    @Prop({
        type: Object,
        required: false,
        default: () => {
            return {
                image: false,
                map: false,
                list: false,
            };
        },
    })
    dropTarget: { image: boolean; map: boolean; list: boolean };

    @Prop({
        type: Boolean,
        required: false,
        default: false,
    })
    modifyDisabled: boolean;
    //#endregion prop

    //#region variable
    private airConditionerSvg = require('../images/device-air-conditioner.svg');
    private imageVideoSource = require('../images/device-source-video.svg');
    private imageTrackSource = require('../images/device-source-track.svg');
    //#endregion variable

    //#region Vue Life
    private mounted(): void {
        this.setScale();
        let self = this;
        window.onresize = function (event) {
            self.setScale();
        };
    }
    //#endregion Vue Life

    //#region Drag & Drop
    private dropMap(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }

        const { mousePosition, size } = this.value.dropItem;

        let emapDeviceInfoRef: any = this.$refs.emapDeviceInfo;
        let left: number = event.layerX + mousePosition.x - size.width / 2;
        let top: number = event.layerY + mousePosition.y - this.value.dropItem.size.height / 2;
        this.dropTarget.map = false;

        // positionShow
        if (this.value.dropItem.device.positionShow === null) {
            this.value.dropItem.device.positionShow = { x: 0, y: 0 };
        }
        this.value.dropItem.device.positionShow.x = left;
        this.value.dropItem.device.positionShow.y = top;

        // resolve device scale
        if (
            !this.value.backgroundItem.showSize ||
            !this.value.backgroundItem.showSize.width ||
            !this.value.backgroundItem.showSize.height ||
            isNaN(this.value.backgroundItem.showSize.width) ||
            isNaN(this.value.backgroundItem.showSize.height) ||
            this.value.backgroundItem.showSize.width <= 0 ||
            this.value.backgroundItem.showSize.height <= 0
        ) {
            this.setScale();
        }

        if (
            this.value.backgroundItem.showSize &&
            this.value.backgroundItem.showSize.width &&
            this.value.backgroundItem.showSize.height &&
            !isNaN(this.value.backgroundItem.showSize.width) &&
            !isNaN(this.value.backgroundItem.showSize.height) &&
            this.value.backgroundItem.showSize.width > 0 &&
            this.value.backgroundItem.showSize.height > 0
        ) {
            const horizontal: number = this.value.backgroundItem.showSize.width;
            const vertical: number = this.value.backgroundItem.showSize.height;

            this.value.dropItem.device.positionScale = {
                horizontal: left / horizontal,
                vertical: top / vertical,
            };
        }

        this.value.clearDropItem();
        if (emapDeviceInfoRef) {
            emapDeviceInfoRef.initSelectionItem();
        }
    }

    private dragMapOver(event: any): boolean {
        event.preventDefault();

        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.map = true;
    }

    private dragMapLeave(event: any): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.dropTarget.map = false;
    }

    private dragSourceStart(event: any, device: Model.IEmapDevice): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        this.value.dropItem.device = device;
        this.value.focusDevice = device;
        if (event.offsetX && event.offsetY) {
            this.value.dropItem.mousePosition.x = event.offsetX;
            this.value.dropItem.mousePosition.y = event.offsetY;
        }
        if (event.srcElement && event.srcElement.clientWidth && event.srcElement.clientHeight) {
            this.value.dropItem.size.width = event.srcElement.clientWidth;
            this.value.dropItem.size.height = event.srcElement.clientHeight;
        }
    }

    private dragSourceOver(event: any, device: Model.IEmapDevice): void {
        // 判斷上很常觸發, 所以先不做事
        // this.value.clearDropItem();
    }
    //#endregion Drag & Drop

    //#region device
    private showDeviceActive(device: Model.IEmapDevice): boolean {
        return device.positionShow !== null && !isNaN(device.positionShow.x) && !isNaN(device.positionShow.y);
    }

    private showDeviceSelection(device: Model.IEmapDevice): boolean {
        if (this.value.focusDevices.length > 0) {
            return !!this.value.focusDevices.find((n) => n.objectId === device.objectId);
        }

        return device.objectId === this.value.focusDevice.objectId;
    }
    //#endregion device

    //#region resolve
    private resolveIsDraggable(): boolean {
        if (this.modifyDisabled) {
            return false;
        }
        if (this.value.backgroundItem.image != '') {
            return true;
        }
        return false;
    }

    private resolveDeviceKey(key: string, device: Model.IEmapDevice): string {
        return `${key}-${device.index}`;
    }

    //#endregion resolve

    //#region image
    private removeImage() {
        EmapDialog.Remove(this.value.i18n.removeContent, this.value.i18n.removeTitle, this.value.i18n.removeButtonYes, this.value.i18n.removeButtonNo)
            .then((result: any) => {
                if (result.isDismissed) {
                    this.clearImageItem();
                }
            })
            .catch((e: any) => {
                console.error(e);
            });
    }

    private clearImageItem() {
        this.value.clearSelf();
    }
    //#endregion image

    //#region set scale
    private setScale() {
        // let modifyImageDivRef: any = this.$refs.modifyImageDiv;
        let modifyImageImgRef: any = this.$refs.modifyImageImg;

        // Maybe no yet get ref
        if (!modifyImageImgRef) {
            return false;
        }

        const { clientWidth, clientHeight } = modifyImageImgRef;

        if (clientWidth && clientHeight && clientWidth > 0 && clientHeight > 0) {
            let backgroundItem: Model.IEmapBackgroundItem = JSON.parse(JSON.stringify(this.value.backgroundItem));

            const width: number = clientWidth;
            const height: number = clientHeight;

            backgroundItem.showSize = {
                width: clientWidth,
                height: clientHeight,
            };
            if (
                backgroundItem.showSize.width !== this.value.backgroundItem.showSize.width ||
                backgroundItem.showSize.height !== this.value.backgroundItem.showSize.height
            ) {
                this.value.backgroundItem = backgroundItem;
            }
        }

        this.value.resetSourcePosition();
    }
    //#endregion set scale

    //#region emit
    private clickDevice(event: any, device: Model.IEmapDevice) {
        this.$emit('click-device', event, device);
    }

    private inputModel() {
        this.$emit('input', this.value);
    }
    //#endregion emit
}
